        },
        async bindDOM() {
            // console.log(window.ethereum)
            if(window.ethereum && (window.ethereum.isMetaMask || window.ethereum.isTrust)) {
                let web3 = new Web3(window.ethereum);
                // // console.log(web3)

                const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
                const address = accounts[0];
                // // console.log(account, accounts)

                let message = `CapitalWhale User Id: ${this.clientId}`;

                let signature = (await web3.eth.personal.sign(web3.utils.fromUtf8(message), address));
                // let recoreved = await web3.eth.personal.ecRecover(message, signature);

