                </div>

                <!-- <p class="window-text">{{$localizeFilter('middleName')}}</p>
                <div class="input-field" :class="isEditing ? '' : 'disabled'">
                    <input id="middleName" class="custom-input" type="text" v-model.trim="user.middleName"/>
                </div> -->
                
                <div class="email">
                    <p class="title">{{$localizeFilter('your_email')}}</p>
                    <p class="text">{{user.email}}</p>
                </div>

                <div class="email">
                    <p class="title">{{$localizeFilter('sponsors_email')}}</p>
                    <p class="text">{{user.sponsorEmail}}</p>
                </div>

                <div class="email">
                    <p class="title">{{$localizeFilter('telegram_name')}}</p>
                    <p class="text">{{'@' + user.telegramName}}</p>
                </div>

                <!-- <div class="telegram">
                    <p class="title">{{$localizeFilter('telegram_name') + ':'}}</p>
                    <p class="text">{{user.telegram}}</p>
                </div> -->
                <!-- <p class="window-text">{{$localizeFilter('telegram_name')}}</p>
                <div class="input-field" :class="isEditing ? '' : 'disabled'">
                    <input id="telegramName" class="custom-input" type="text" v-model.trim="user.telegramName"/>
                </div> -->

                <p class="window-text">{{$localizeFilter('referral_link')}}</p>
                <div class="input-field" :class="'disabled'">
                    <input id="referralLink" class="custom-input" type="text" v-model.trim="user.referralLink"/>
                    <button class="custom-input-button copy-button" @click="copy(user.referralLink)"><i class="material-icons">content_copy</i></button>
                </div>

                <div class="buttons-block">
                    <button class="basic-button" v-if="!isEditing" @click="startEditing">{{$localizeFilter('edit')}}</button>
                    <button class="basic-button" v-if="isEditing" @click="endEditing">{{$localizeFilter('save')}}</button>
                    <!-- <button class="basic-button contact-sponsor" @click="contactSponsor">{{$localizeFilter('contact_sponsor')}}</button> -->
                </div>

         </div>
      </div>


      <DOMWindow :DOMWallet="user.DOMWallet" :DOMAmount="user.DOMAmount" :clientId="user.id" @domBinded="domBinded"/>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import DOMWindow from '@/components/DOMWindow'

export default defineComponent({
    data: () => ({
        user: {
        },
        isEditing: false
    }),
    components: {
        DOMWindow
    },
    methods: {
        async contactSponsor() {
            try {
                const formData = {userId: this.user.sponsorId}
                const chatId = await this.$store.dispatch('createRoomWithUserId', formData)
                this.$router.push(`/cabinet/messenger?chatId=${chatId}`)
            } catch(e) {
                this.$error(e)
            } 
        },
        startEditing() {
            this.isEditing = true
        },
        async endEditing() {
            try {
                const formData = {firstName: this.user.firstName, lastName: this.user.lastName, telegramName: this.user.telegramName}
                await this.$store.dispatch('editProfile', formData)
                this.$message(localizeFilter('profile_updated'))
                this.isEditing = false
            } catch(e) {

                this.$error(e)
            } 
        },
        domBinded(){
            this.loadData();
        },
         copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    // console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
        },
        async loadData() {
            try {
                let user = await this.$store.dispatch('getProfile')
                user.referralLink=`https://${location.host}/#/register?token=${user.referralToken}`
                this.user = user
            } catch(e) {
                this.$error(e)
            } 
        },
    },
    
    mounted() {
        this.loadData();
    }
})
</script>

